import { ADD_PARTNERS } from './ActionTypes';

export const Partners = (state = {
    isLoading: true,
    errMess: null,
    partners: []
}, action) => {
    switch (action.type) {
        case ADD_PARTNERS:
            console.log('Partners = ', action.payload);
            return {
                ...state,
                partners: action.payload
            }
        default:
            return state;
    }
};